<template>
  <div id="DetailInfo">

    <div id="detailInfo-main-part3" v-if="step===3">

      <div class="detailInfo-step3-div">
        <el-steps :space="200" :active="step">
          <el-step title=" 1" icon="edit"></el-step>
          <el-step title=" 2" icon="picture"></el-step>
          <el-step title=" 3" icon="information"></el-step>
        </el-steps>
      </div>

    <transition name="fade">
      <div class="detaiInfo-form">
        <el-form ref="form" :model="form" label-width="250px">

          <el-form-item label="">
            <el-radio-group v-model="form.gender">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>

          <el-form-item label="">
              <el-autocomplete
             class="inline-input"
             v-model="form.school"
             :fetch-suggestions="querySearch"
             placeholder=""
             :trigger-on-focus="false"
             @select="handleSelect"
           ></el-autocomplete>
          </el-form-item>

          <el-form-item label=",">
            <el-date-picker
             v-model="form.birthday"
             type="date"
             placeholder=""
             :picker-options="pickerOptions">
           </el-date-picker>
          </el-form-item>

           <el-form-item label="">
               <el-input v-model="form.name"></el-input>
           </el-form-item>

           <el-form-item label="">
              <el-input v-model="form.address"></el-input>
           </el-form-item>

           <el-form-item>
             <el-button type="primary" @click="_onSubmitStep3"></el-button>
             <el-button type="warning"></el-button>
           </el-form-item>

        </el-form>
      </div>
      </transition>

    </div>

    <transition name="fade">
      <div id="detailInfo-main-part2" v-if="step===2">
      <div class="detailInfo-step2-div">
        <el-steps :space="200" :active="2">
          <el-step title=" 1" icon="edit"></el-step>
          <el-step title=" 2" icon="picture"></el-step>
          <el-step title=" 3" icon="information"></el-step>
        </el-steps>
      </div>
        <div class="headpic-uploader move-lvzu">
          <div class="container">
            <div class="imageBox">
              <div class="thumbBox"></div>
              <div class="spinner" style="display: none">Loading...</div>
            </div>
            <div class="action">
              <div class="new-contentarea tc">
                <a href="javascript:void(0)" class="upload-img">
                <label for="upload-file"></label>
                </a>
                <input type="file"  class="" name="upload-file" id="upload-file" />
              </div>
              <input type="button" id="btnCrop"  class="Btnsty_peyton" value="">
              <input type="button" id="btnZoomIn" class="Btnsty_peyton" value="+"  >
              <input type="button" id="btnZoomOut" class="Btnsty_peyton" value="-" >
              <input type="button" id="headpic-sub-button"  value="" @click="_onSubmitStep2">
            </div>
            <div class="cropped"></div>
          </div>
        </div>
      </div>
    </transition>

  </div>
</template>

<script>
/*
 @Author:lvzu
 @Date:2017/9/5

 *
 *
 *JPGPNGSVG
 *
 *

 */
import './cropbox.js'
import myTools from '../../tools/myTools.js'
import schools from './schools.js'
import _asyncUpload from '../../module/OssUpload'
// import AliyunOssUpload from '../../component/AliyunOssUpload.vue'


export default {

  name: "DetailInfo",

  data() {
    return {
      //
      hostname: '47.94.129.112',
      //
      step: 2,

      //
      form: {
        name: '',
        address: '',
        birthday: '',
        gender: '',
        school: '',
        headpic_url: '',
      },
      schools: '',

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 8.64e7;
        }
      },

      cropper: '',
      upFile: '',
    }
  },

  methods: {

    headpicUpload() {
      const t = this
      let username = localStorage.username
      if (!t.upFile) {
        //
        t.$alert('(/)/ ', '', {
          confirmButtonText: '',
          callback: action => {
            t.$message({
              type: 'info',
              message: ''
            });
          }
        });
        return
      }
      if (!t._isImg()) {
        return
      }
      var credentials, client
      var imgFile = myTools._convertBase64UrlToFile(t.cropper.getDataURL());
      _asyncUpload(username, t, imgFile)
    },

    querySearch(queryString, cb) {
      var schools = this.schools;
      var results = queryString ? schools.filter(this.createFilter(queryString)) : schools;
      //  callback 
      cb(results);
    },

    createFilter(queryString) {
      return (school) => {
        return (school.value.indexOf(queryString.toLowerCase()) === 0);
      };
    },

    _onSubmitStep2() {
      this.headpicUpload()
      // this.step = 3
    },

    _onSubmitStep3() {
      const t = this
      //

      //
      fetch(`http://${t.hostname}/api/set_detailInfo`, {
          method: 'post',
          body: 'username=' + localStorage.username + '&name=' + t.form.name + '&address=' + t.form.address + '&birthday=' + t.form.birthday + '&gender=' + t.form.gender + '&school=' + t.form.school + '&headpic_url=' + '',
          headers: {
            "Accept": "application/json",
            "Content-Type": "application/x-www-form-urlencoded"
          },
        })
        .then(re => re.json())
        .then(re => {
          if (re.msg == "success") {
            //
            this.$message({
              message: '',
              type: 'success'
            });
            setTimeout(function() {
              t.$router.push({
                path: '/main'
              })
            }, 1500)
          }
        })
    },

    _isImg() {
      const t = this
      var _upFile = this.upFile
      if(!_upFile){
        return false
      }
      if (!(_upFile.type == 'image/jpeg' || _upFile.type == 'image/png' || _upFile.type == 'image/svg+xml')) {
        //
        t.$alert('JPEGPNG(/)/ ', '', {
          confirmButtonText: '',
        });
        return false
      }
      return _upFile.type
    },

  //methods
  },


  mounted() {

    const t = this
    if (!localStorage.username) {
      t.$message({
        message: '',
        type: 'warning'
      });
      setTimeout(function() {
        t.$router.push({
          path: '/register'
        })
      }, 1500)
      return
    }

    this.schools = schools
    // console.log("DetailInfo mounted");

    var options = {
      thumbBox: '.thumbBox',
      spinner: '.spinner',
      imgSrc: '../../../static/imgs/avatar.jpg'
    }
    t.cropper = $('.imageBox').cropbox(options);
    $('#upload-file').on('change', function() {
      t.upFile = this.files[0]
      // console.log(t.upFile);

      //
      t._isImg()
      var reader = new FileReader();
      reader.onload = function(e) {
        options.imgSrc = e.target.result;
        t.cropper = $('.imageBox').cropbox(options);
      }
      reader.readAsDataURL(this.files[0]);
      // this.files = [];
    })
    $('#btnCrop').on('click', function() {
      var img = t.cropper.getDataURL();
      if (img) {
        $('.cropped').html('');
        $('.cropped').append('<img src="' + img + '" align="absmiddle" style="width:64px;margin-top:4px;border-radius:64px;box-shadow:0px 0px 12px #7E7E7E;" ><p>64px*64px</p>');
        $('.cropped').append('<img src="' + img + '" align="absmiddle" style="width:128px;margin-top:4px;border-radius:128px;box-shadow:0px 0px 12px #7E7E7E;"><p>128px*128px</p>');
        $('.cropped').append('<img src="' + img + '" align="absmiddle" style="width:180px;margin-top:4px;border-radius:180px;box-shadow:0px 0px 12px #7E7E7E;"><p>180px*180px</p>');
      }
    })
    $('#btnZoomIn').on('click', function() {
      t.cropper.zoomIn();
    })
    $('#btnZoomOut').on('click', function() {
      t.cropper.zoomOut();
    })

  //mouted
  },


}
</script>


<style lang="scss" scoped>
@import "./DetailInfo.scss"

</style>



